.centered{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 50px;
}

.reg-block{
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
}

.reg-input{
    display: block;
    width: 400px;
}

.login-btn-y{
    background-color: #FFCF08;
    border-radius: 10px;
    padding: 15px 50px;
    outline: none;
    border: none;
    cursor: pointer;
}

@media screen and (max-device-width: 600px) {
    .reg-input{
        width: 280px !important;
    }
}

@media screen and (max-device-width: 600px) {
    .reg-input{
        width: 200px !important;
    }
}